import { Component, OnInit, Input } from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';

@Component({
  selector: 'kt-juniper',
  templateUrl: './juniper.component.html',
  styleUrls: ['./juniper.component.scss']
})
export class JuniperComponent implements OnInit {

  @Input() options
  @Input() validation

  form: FormGroup;

  constructor(public fb: FormBuilder,) {}

  ngOnInit() {
    this.form = this.fb.group({
      chips: [this.options.data.junos, []],
    });
    this.form.get('chips').valueChanges.subscribe(val => {
      // console.log(val)
      this.options.data.junos = val
    });
  }

}
