import { Component, OnInit, Input } from '@angular/core';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-obj-commands-form',
  templateUrl: './obj-commands-form.component.html',
  styleUrls: ['./obj-commands-form.component.scss']
})
export class ObjCommandsFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  private type = 0;


  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    console.log(this.options)
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

}
