import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model CMD
import { CMD } from '../../../../../partials/layout/tacgui/_forms/obj-commands-form/obj-commands.model'
//Service//
import { CMDService } from '../cmd.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(true)

  formParameters = {
    action: 'add',
    data: new CMD,
    old_data: new CMD
  }

  constructor(
    private toastr:ToastrService,
    private cmd_service: CMDService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(+params.get('id'))
    });
  }

  fillForm(id:number){
    console.log(id)
    this.cmd_service.get(id).subscribe( data => {
      console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }

      Object.assign(this.formParameters.data, this.makeClone(data))
      Object.assign(this.formParameters.old_data, this.makeClone(data))

      this.loadingForm.next(false)

    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    this.loadingForm.next(true)
    let data = this.makeClone(this.formParameters.data)
    let old_data = this.makeClone(this.formParameters.old_data)

    // console.log(JSON.stringify(this.formParameters.data), JSON.stringify(this.formParameters.old_data))
    if (JSON.stringify(data) == JSON.stringify(old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }
    data.junos = (data.junos.length) ? data.junos.join(',') : '';
    console.log(data)

    this.cmd_service.save(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('CMD Set Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
