import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model CMD
import { CMD } from '../../../../../partials/layout/tacgui/_forms/obj-commands-form/obj-commands.model'
//Service//
import { CMDService } from '../cmd.service'

@Component({
  selector: 'kt-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new CMD,
    old_data: new CMD
  }

  constructor(
    private toastr:ToastrService,
    private cmd_service: CMDService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  add(){
    this.loadingForm.next(true)
    let data = this.makeClone(this.formParameters.data)

    data.junos = (data.junos.length) ? data.junos.join(',') : '';
    console.log(data)
    this.cmd_service.add(data).subscribe(data => {
      // console.log(data)
      this.validation.next(data.error.validation)
      // console.log(this.validation.getValue())
      if(data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.cmd) {
        this.toastr.success('Command Added!');
        this.router.navigate(['../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
