import { Component, OnInit, Input } from '@angular/core';
//CDK
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';

@Component({
  selector: 'kt-general',
  templateUrl: './general.component.html',
  styleUrls: ['./general.component.scss']
})
export class GeneralComponent implements OnInit {

  @Input() options
  @Input() validation
  private argAction = 1
  private newArg = ''

  constructor() { }

  ngOnInit() {
  }

  addArg(){
    if (this.newArg == '') return
    let regex = /^\/.*\/\s{0,}$/;
    let text = this.newArg;
    text = ( text && !regex.test( text ) ) ? '/'+text+'/' : text

    for (let i = 0; i < this.options.data.cmd_attr.length; i++) {
        if (this.options.data.cmd_attr[i].arg == text) return;
    }

    this.options.data.cmd_attr.push({action: this.argAction, arg: text})
    this.newArg = ''
  }

  delArg(index){
    this.options.data.cmd_attr.splice(index, 1)
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.options.data.cmd_attr, event.previousIndex, event.currentIndex);
  }

}
